"use client";

import { useState } from "react";
import DraggableDiv from "./DraggableDiv";

const InfoCard = () => {
  const [visible, setVisible] = useState(false);

  return (
    <div
      className="rounded-4xl flex flex-col gap-2 bg-stone-300 w-full h-full shadow-[inset_0px_0px_8px_rgba(0,0,0,0.4)] p-10"
      onClick={() => setVisible(true)}
    >
      <div className="rounded-2xl bg-stone-700 w-100 h-40 shadow-[inset_0px_0px_8px_rgba(0,0,0,0.4)] p-10"></div>
      <h1 className="font-semibold text-stone-800">Project Heading</h1>
      <p className="font-extrabold text-stone-600">
        Lorem ipsum dolor sit amet consectetur adipisicing elit...
      </p>

      {/* Render DraggableDiv outside of the InfoCard div to avoid accidental closure */}
      {visible && <DraggableDiv visible={visible} closeFunction={() => setVisible(false)} />}
    </div>
  );
};

export default InfoCard;
